body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Button-Nice {
  padding: 10px;
  background: rgba(204, 187, 204, 0.4);
  border: 1px solid #997a99;
  border-radius: 50%;
  color: #fff;
  width: 100px;
  height: 100px;
}

.Button-Nice:focus,
.Button-Nice:active {
  box-shadow: 0 0 3pt 2pt #997a99;
  outline: none;
}
.Controls {
  z-index: 1;
  display: flex;
}

.Controls-item {
  margin: 16px;
}

.Controls-item:first-child {
  margin-left: 0;
}

.Controls-item:last-child {
  margin-right: 0;
}
.Kale-Wrapper {
  position: fixed;
  top: 0;
  left: 50%;
}

.Kale-Spin {
  -webkit-animation: Kale-spin infinite 20s linear;
          animation: Kale-spin infinite 20s linear;
}

.Kale-Pop {
  -webkit-animation: KalePop infinite 2s linear;
          animation: KalePop infinite 2s linear;
  height: 40vmin;
}

@-webkit-keyframes KalePop {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }

  18.75%, 43.75%, 68.75%, 93.75% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes KalePop {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }

  18.75%, 43.75%, 68.75%, 93.75% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes Kale-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Kale-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.Garlic-Wrapper {
  position: fixed;
  top: 37%;
  left: 5%;
}

.Garlic-Salsa {
  -webkit-animation: rubberBand infinite 4s linear;
          animation: rubberBand infinite 4s linear;
  height: 40vmin;
}

/* https://daneden.github.io/animate.css/ */
@-webkit-keyframes rubberBand {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1);
  }

  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
@keyframes rubberBand {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1);
  }

  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

/* https://daneden.github.io/animate.css/ */
@-webkit-keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}
@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}
.Mint-Wrapper {
  position: absolute;
  top: 0;
  right: 0;
}

.Mint-Salsa {
  -webkit-animation: DillSway infinite 4s linear;
          animation: DillSway infinite 4s linear;
}


/* https://daneden.github.io/animate.css/ #jello */
@-webkit-keyframes DillSway {
  from, 11.1% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  22.2% {
    -webkit-transform: skewX(-6.5deg) skewY(-6.5deg);
            transform: skewX(-6.5deg) skewY(-6.5deg);
  }

  33.3% {
    -webkit-transform: skewX(3.25deg) skewY(3.25deg);
            transform: skewX(3.25deg) skewY(3.25deg);
  }

  44.4% {
    -webkit-transform: skewX(-3.125deg) skewY(-3.125deg);
            transform: skewX(-3.125deg) skewY(-3.125deg);
  }

  55.5% {
    -webkit-transform: skewX(1.5625deg) skewY(1.5625deg);
            transform: skewX(1.5625deg) skewY(1.5625deg);
  }

  66.6% {
    -webkit-transform: skewX(-0.78125deg) skewY(-0.78125deg);
            transform: skewX(-0.78125deg) skewY(-0.78125deg);
  }

  77.7% {
    -webkit-transform: skewX(0.390625deg) skewY(0.390625deg);
            transform: skewX(0.390625deg) skewY(0.390625deg);
  }

  88.8% {
    -webkit-transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
            transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
  }

  to {
    -webkit-transform: skewX(0deg) skewY(0deg);
            transform: skewX(0deg) skewY(0deg);
  }
}
@keyframes DillSway {
  from, 11.1% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  22.2% {
    -webkit-transform: skewX(-6.5deg) skewY(-6.5deg);
            transform: skewX(-6.5deg) skewY(-6.5deg);
  }

  33.3% {
    -webkit-transform: skewX(3.25deg) skewY(3.25deg);
            transform: skewX(3.25deg) skewY(3.25deg);
  }

  44.4% {
    -webkit-transform: skewX(-3.125deg) skewY(-3.125deg);
            transform: skewX(-3.125deg) skewY(-3.125deg);
  }

  55.5% {
    -webkit-transform: skewX(1.5625deg) skewY(1.5625deg);
            transform: skewX(1.5625deg) skewY(1.5625deg);
  }

  66.6% {
    -webkit-transform: skewX(-0.78125deg) skewY(-0.78125deg);
            transform: skewX(-0.78125deg) skewY(-0.78125deg);
  }

  77.7% {
    -webkit-transform: skewX(0.390625deg) skewY(0.390625deg);
            transform: skewX(0.390625deg) skewY(0.390625deg);
  }

  88.8% {
    -webkit-transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
            transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
  }

  to {
    -webkit-transform: skewX(0deg) skewY(0deg);
            transform: skewX(0deg) skewY(0deg);
  }
}
.App {
  text-align: center;
}

