.Kale-Wrapper {
  position: fixed;
  top: 0;
  left: 50%;
}

.Kale-Spin {
  animation: Kale-spin infinite 20s linear;
}

.Kale-Pop {
  animation: KalePop infinite 2s linear;
  height: 40vmin;
}

@keyframes KalePop {
  0% {
    transform: scale(1);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: scale(0.7);
  }

  18.75%, 43.75%, 68.75%, 93.75% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes Kale-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}